h2 {
    margin: 0;
    padding: 20px;
}

p {
    padding: 20px;
}

.go {
    display: inline-block;
    border-radius: 30px;
    padding: 20px;
    background-color: skyblue;
    color: #fff;
    text-decoration: none;
}

.go:hover {
    background-color: blue;
}

.main {
    padding: 30px 0;
    background-image: url("../../images/background.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
}
